'use client'

import { ReactNode, useState } from 'react'
import { I18nextProvider, useTranslation } from 'react-i18next'
import i18n from '@/lib/i18n'
import { ThemeProvider } from '@/contexts/theme-context'

function LanguageSwitcher() {
  const { i18n: i18Instance, t } = useTranslation()

  return (
    <div className="fixed bottom-4 right-4 flex items-center gap-2 rounded-full bg-slate-900/90 px-3 py-1.5 text-xs text-slate-200 shadow-lg ring-1 ring-slate-700">
      <span className="hidden sm:inline text-slate-400">{t('footer.language')}</span>
      <button
        type="button"
        onClick={() => i18Instance.changeLanguage('pt-BR')}
        className={`px-2 py-0.5 rounded-full ${
          i18Instance.language === 'pt-BR' ? 'bg-slate-200 text-slate-900' : 'text-slate-300 hover:text-white'
        }`}
      >
        {t('footer.portuguese')}
      </button>
      <span className="text-slate-600">•</span>
      <button
        type="button"
        onClick={() => i18Instance.changeLanguage('en-US')}
        className={`px-2 py-0.5 rounded-full ${
          i18Instance.language === 'en-US' ? 'bg-slate-200 text-slate-900' : 'text-slate-300 hover:text-white'
        }`}
      >
        {t('footer.english')}
      </button>
    </div>
  )
}

export function Providers({ children }: { children: ReactNode }) {
  // Garantir uma única instância estável no client
  const [instance] = useState(() => i18n)

  return (
    <I18nextProvider i18n={instance}>
      {children}
      <LanguageSwitcher />
    </I18nextProvider>
  )
}

